import React, { Component } from "react";

class Card extends Component {
    render() {
        const target = (this.props.project.link === "javascript:void(0);") ? "" : "_blank";
        const cardClass = this.props.project.size + " card";
        return (
            <div className={cardClass} style={{backgroundImage: `url(${this.props.project.img})`}}>
                <a href={this.props.project.link} target={target}>
                    <div>
                        <div className="card-transparency">
                            <h2 className="card-header text-header scale-text-body">{  this.props.project.header  }</h2>
                        </div>
                    </div>
                </a>
            </div>
        )
    }
}

export default Card;