class App extends Component {
    componentDidMount() {
        let vecScript = document.createElement("script");
        vecScript.async = true;
        vecScript.src = "Vec3D.js";
        document.body.appendChild(vecScript);
        
        let animScript = document.createElement("script");
        animScript.async = true;
        animScript.src = "anim.js";
        document.body.appendChild(animScript);
    }

    render() {
        return (
            <div>
                <div className="content-container">
                    <canvas id="bg-anim"/>
                    <Splash />
                    <h2 className="section-header">Projects:<div className="header-underline"></div></h2>
                    <div className="card-deck" id="gallery">
                        {projects.map((proj) => (
                            <Card key={proj.header} project={proj}/>
                        ))}
                    </div>
                    <div className="footer-wrapper">
                        <h2 className="section-header">Contact Me:<div className="header-underline"></div></h2>
                        <p className="footer-content text-body scale-text-body">
                            Email: &emsp;&emsp;ianedougherty01@gmail.com<br/>
                            Resume: &ensp;&nbsp;<a className="resume-link" href="https://idougherty.github.io/portfolio/ian's_resume.pdf" target="_blank">Click to download</a><br/>
                        </p>
                    </div>
                </div>
            </div>
        )
    }
}

ReactDOM.render(<App />, document.getElementById('root'))

const CARD_SIZES = {
    "sm card": 2,
    "md card": 3,
    "lg card": 4,
}
const G_WIDTH = 6;

let gallery = document.getElementById("gallery");
let x0 = 1;
let y0 = 1;

let row = [];
let row_height = 0;
let row_width = 0;
let area = 0;

for(const element of gallery.children) {
    row.push(element);
    let size = CARD_SIZES[element.className];
    area += size * size;
    row_width += size;
    row_height = Math.max(size, row_height);

    if(row_width >= G_WIDTH && G_WIDTH * row_height == area) {
        //place elements
        let x = x0;
        let y = y0;

        for(const card of row) {
            let size = CARD_SIZES[card.className];

            card.style.gridColumnStart = x;
            card.style.gridRowStart = y;
            
            card.style.gridColumnEnd = x + size;
            card.style.gridRowEnd = y + size;

            y += size;

            if(y + size > y0 + row_height) {
                x += size;
                y = y0;
            }
        }

        x0 = 1;
        y0 += row_height;

        row = [];
        row_width = 0;
        row_height = 0;
        area = 0;
    }
}

let cardBGs = document.getElementsByClassName("card-transparency");

let hue = 0;

for(const element of cardBGs) {
    const hue2 = hue + 360 / cardBGs.length;
    element.style.background = `linear-gradient(hsla(${hue}, 40%, 50%, .3), hsla(${hue2}, 40%, 50%, .7))`;
    hue = hue2;
}
